.login-form-2 .form-control{
    background-color:rgba(0,0,0,0) !important;
 
    
}
.login-container{
    margin-top: 15%;
    justify-content: center;
}
.login-form-1{
    border-radius: 9px;
    box-shadow: 5px 5px 8px 5px rgba(0, 0, 0, 0.2), 0 20px 26px 0 rgba(0, 0, 0, 0.19);
    padding: 2%;
    background: #ffffff;
}
.login-form-1 h3{
    text-align: center;
    color: #283040;
}
.login-form-2{
    border-radius: 9px;
    padding: 2%;
    background: #f7f7f700;
}
.login-form-2 h3{
    text-align: center;
    color: #fff;
}
.login-container form{
    padding: 10%;
}
.btnSubmit
{
    width: 50%;
    border-radius: 1rem;
    padding: 1.5%;
    border: none;
    cursor: pointer;
}
.login-form-1 .btnSubmit{
    color: #fff;
    background-color: #0062cc;
}
.login-form-2 .btnSubmit{
    background-color: #000;
    color: #0062cc;
    border-radius: 0rem;
}


